import React from "react";
import Icon from "@material-ui/core/Icon";
import { display } from "@material-ui/system";
import Textarea from "react-textarea-autosize"; 
import Card from "@material-ui/core/Card";
import Button from "@material-ui/core/Button";
import ListItem from './ListItem'

import CardContainer from "./CardContainer";

class ReusableComponent extends React.Component {
  state={
    formOpen: false,
    text: " ",
    lists: [],
    cards:[],
    
}

openForm=()=>{
  this.setState({
      formOpen: true
  })  
}

closeForm=()=>{
    this.setState({
        formOpen: false
    })
}


handleInputChange=(e)=>{
 this.setState({
    text: e.target.value
})
}






renderAddButton=()=>{
const {list} = this.props
const buttonText = list? "Add another list" : "Add another card"
const buttonTextOpacity= list? "white" : "inherit";
const buttonTextColor= list ? 1 : 0.5;
const buttonTextColorBackground= list ?  "rgba(0,0,0,.15)" : "inherit";
return(
    <div 
    onClick={this.openForm}
    style={{
       ...style.openFormButtonGroup,
       opacity: buttonTextOpacity,
       color: buttonTextColor,
       backgroundColor: buttonTextColorBackground
    }}>
   <Icon>add</Icon>
   <p>{buttonText}</p>
    </div>
)
}

renderForm=()=>{
  const {list} = this.props;
  const placeholder = list? "Enter list title..."  
  : "Enter a title for this card...";
  const buttonTitle = list ? "Add List" : "Add Card";
  return(
      <div>
        <Card style={{  
          overflow: "visible",
          minHeight: 80,
          minWidth: 272,
          padding: "6px 8px 2px",
          display: "flex",
          margin: 8
        }} onClick={this.props.deleteList} >
      
         <Textarea
          placeholder={placeholder}
          autoFocus
          onBlur={this.closeForm}
          value={this.state.text} onChange={this.handleInputChange} style={{
               resize: "none",
               width: "100%",
               overflow: "hidden",
               outline: "none",
               border: "none" 
              }}/>
       </Card >  
       <div style={style.formButtonGroup} >
       <Button
       onMouseDown={ list? ()=>{this.props.handleAddList(this.state.text)} : ()=>{this.props.handleAddCard(this.state.text)}} 
       variant="container"
       style={{ color: "white", backgroundColor: "#5aac44" }}  >
       {buttonTitle}{" "}
       </Button>
       <Icon style={{marginLeft: 8, cursor: "pointer" }} >close</Icon>
       </div>
      </div>
  )
 
  }


render(){
return(
   
   <div> 
      {this.state.formOpen ? this.renderForm() : this.renderAddButton()}        
    </div>
   
  )
  }
}


const style ={
  openFormButtonGroup:{
      display: "flex",
      alignItems: "center",
      cursor: "pointer",
      borderRadius: 3,
      height: 36,
      width: 272,
      paddingLeft:10 
  },
  formButtonGroup: {
      marginTop: 8,
      display: "flex",
      alignItems: "center"
    
    }
  }


export default ReusableComponent;













    
      
