class ListContainer extends React.Component {
  state={
    lists: [],
    displaylists: [],
    title: "",
    inputValue: "",
    text: " ",
    cards: [],
    isLoading: false,
  }



  componentDidMount() {
    if(localStorage.token){
      this.getUserLists()
    }
    this.setState({
      isLoading: !this.state.isLoading
    });
  }


  getUserLists = () => {
    fetch('https://pure-sierra-79465.herokuapp.com/lists',{ 
      method: "GET",
      headers: {
        Authorization: `Bearer ${localStorage.token}`
      }
    })
      .then((res) => res.json())
      .then((lists) => {
        console.log(lists.id)
        this.setState({
          lists: lists,
          displaylists: lists,
        });
      });
  }



// POST REQUEST FOR LIST
handleAddList=(text)=>{


  fetch("https://pure-sierra-79465.herokuapp.com/lists", {
   method: "POST",
   headers: {
   'Content-Type': 'application/json',
   Authorization: `Bearer ${localStorage.token}`
   },
   body: JSON.stringify({
     list:{
       title: text,
       cards: [],
       user_id: localStorage.id
     }})     
   })
    .then(res => res.json())
    .then(list => {
    this.setState({
      lists: [...this.state.lists, list]
   })
 
  })
  }




  render() {

   
    return (

    <div className="surface">
      <div className=" listContainer" >
        <div style={styles.listsContainer}> 
             {this.state.lists.map(list=> <ListItem {...list} key={list.id} deleteList={this.deleteList} updateListValue={this.updateListValue}/>)}
             <ReusableComponent list handleAddList={this.handleAddList} deleteList={this.deleteList}/> 
             <Grid style={gridstyle.container}>
                   <Avatar style={Avatastyle.yellowAvatar}>
                  < ChatIcon></ChatIcon>
              </Avatar>
          </Grid>
        </div>
        </div>
      </div>
      
     
   )
    
  }
}


