import React, { Component } from "react";
import CardContainer from "./CardContainer";
import Button from "@material-ui/core/Button";
import ActionButton from "./ReusableComponent";
import ListContainer from "./ListContainer";
import Textarea from "react-textarea-autosize";
import ReusableComponent from "./ReusableComponent";

class ListItem extends Component {
  state = {
    inputValue: "",
    lists: [],
    cards: this.props.cards // I did this so I can grab the the cards from the lists
  };

  handleChange = (e) => {
    this.setState({
      text: e.target.value
    });
  };

  //POST REQUEST FOR THE CARD
  handleAddCard = (text) => {
    console.log(this.props);
    fetch("https://pure-sierra-79465.herokuapp.com/api/v1/cards", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`
      },
      body: JSON.stringify({
        card: {
          text: text,
          list_id: this.props.id
        }
      })
    })
      .then((res) => res.json())
      .then((card) => {
        console.log(card);
        this.setState({
          cards: this.state.cards.concat(card) // cards: [...this.state.cards, card]
        });
      });
  };

  //DELETE CARD
  deleteCard = (id) => {
    console.log(id);
    fetch(`https://pure-sierra-79465.herokuapp.com/cards/${id}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`
      }
    }).then((c) => {
      this.setState({
        cards: this.state.cards.filter((card) => {
          return card.id !== id; // return where the cards ID is not equal to the ID
        })
      });
    });
  };

  //PATCH REQUEST FOR THE CARD
  updateCardValue = (updateText, cardId) => {
    fetch(`https://pure-sierra-79465.herokuapp.com/cards/${cardId}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`
      },
      body: JSON.stringify({
        list_id: this.props.id,
        text: updateText
      })
    })
      .then((res) => res.json())
      .then((card) => {
        console.log(card);
        let cardArr = this.state.cards.map((c) => {
          if (card.id === c.id) {
            c = card;
          }
          return c;
        });
        this.setState({
          cards: cardArr
        });
      });
  };

  render() {
    return (
      <div style={styles.container} onClick={this.props.deleteList}>
        <div onClick={this.updateListValue}>
          <h3>{this.props.title}</h3>
        </div>
        {this.state.cards.map((card) => (
          <CardContainer
            {...card}
            key={card.id}
            updateCardValue={this.updateCardValue}
            deleteCard={this.deleteCard}
          />
        ))}

        <ReusableComponent handleAddCard={this.handleAddCard} />
      </div>
    );
  }
}

const style = {
  openFormButtonGroup: {
    display: "flex",
    alignItems: "center",
    cursor: "pointer",
    borderRadius: 3,
    height: 36,
    width: 272,
    paddingLeft: 10
  },
  formButtonGroup: {
    marginTop: 8,
    display: "flex",
    alignItems: "center"
  }
};

const styles = {
  container: {
    backgroundColor: "#dfe3e6",
    borderRadius: 3,
    width: 300,
    padding: 8,
    marginLeft: 9,
    marginRight: 8,
    marginTop: 8,
    fontFamily:
      "-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Noto Sans,Ubuntu,Droid Sans,Helvetica Neue,sans-serif",
    boxShadow: "0 1px 0 rgba(9,30,66,.25)",
    height: "100%"
  }
};

export default ListItem;
