import React, { Component } from 'react';
// import CardItem from './CardItem';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import ReusableComponent from './ReusableComponent'
import {Draggable} from "react-beautiful-dnd";
import Icon from "@material-ui/core/Icon";
import Textarea from "react-textarea-autosize"; 
import { Button } from '@material-ui/core';
class CardContainer extends React.Component{
    state={
      inputValue: " ",
      text: "",
      isInEditMode: false,
      formOpen: true,
      lists: [],
      cards:[],
      
    } 


  changeEditMode=()=>{
    this.setState({
      isInEditMode: !this.state.isInEditMode
    })
    
  }

  handleChange=(e)=>{
    this.setState({
      editValue: e.target.value
    })
  }




  renderEditView=()=>{
    return <div>
    <Textarea 
    autoFocus
     type="text"
     value={this.state.editValue}
     defaultValue={this.props.text}
     onChange={this.handleChange}
     style={{
      resize: "none",
      width: "100%",
      overflow: "hidden",
      outline: "none",
      border: "none",
      minHeight: 80,
      minWidth: 272,
      padding: "6px 8px 2px"
     }}
    /> 
    <Button onClick={()=>{
      this.props.updateCardValue(this.state.editValue,this.props.id)
      this.changeEditMode()
    }} 
    style={{ color: "white", backgroundColor: "#5aac44" }} 
    >Save</Button>
    <Icon onClick={this.changeEditMode}>close</Icon>
    <Icon onClick={()=>{
      this.props.deleteCard(this.props.id)
      this.changeEditMode()
      }}>delete
    </Icon>
  </div>
    
  }
  renderDefaultView=()=>{
    return <Card style={styles.CardContainer} onClick={this.changeEditMode}>
    <Typography gutterBottom>
      <CardContent>
    <div>     
   <p>{this.props.text}</p>
   </div>
   </CardContent>
   </Typography> 
  </Card>  
  
  }

  render(){
      return(
          <div >
            {this.state.isInEditMode ?
             this.renderEditView(): 
            this.renderDefaultView()
            }
           
      </div> 
     
      )}  
  }


 const styles={
  CardContainer:{
    marginBottom:8
  }
}



const style ={
  openFormButtonGroup:{
      display: "flex",
      alignItems: "center",
      cursor: "pointer",
      borderRadius: 3,
      height: 36,
      width: 272,
      paddingLeft:10 
  },
  formButtonGroup: {
      marginTop: 8,
      display: "flex",
      alignItems: "center"
    
    }
  }




export default CardContainer;