import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import "./App.css";
import Login from "./Login";
import SignUp from "./SignUp";
import Header from "./Header";
import Home from "./Home";
import Footer from './Footer'
import "semantic-ui-css/semantic.min.css";
import ListContainer from "./ListContainer";
import Grid from '@material-ui/core/Grid';
import ChatIcon from '@material-ui/icons/Chat';
import Avatar from '@material-ui/core/Avatar';

class App extends React.Component {
  
      state = {
        loggedIn: localStorage.token ? true : false,
        lists: [],
        displaylists: [],
        title: "",
        isLoading: false
      }

      logout = ()=>{
        localStorage.clear()   
        this.setState({loggedIn:false})
      }

      login = () => {
        this.setState({loggedIn:true})
      }



  render() {
    return (
      <div>
      <BrowserRouter>
      <Header loggedIn={this.state.loggedIn} logout={this.logout} />
      
      {localStorage.token ?
       <Route  path='/' render={(routerProps)=> <ListContainer {...routerProps}/>} />
       :
          <div className="App">
             <Switch> 
              <Route path='/Login' render={(routerProps)=> <Login login={this.login} {...routerProps}/>}/>
              <Route path="/SignUp" component={SignUp} /> 
              <Route path="/" render = {() => <Home /> } />
            </Switch> 
            <Grid style={gridstyle.container}>
                    <Avatar style={Avatastyle.yellowAvatar}>
                  <ChatIcon></ChatIcon>
              </Avatar>
          </Grid>  
        <Footer/>         
        </div> 
      }
        
     </BrowserRouter>
      </div>
    );
  
  }
}




const gridstyle={
  container:{
    alignItems: "bottom",
  
  }
}

const Avatastyle={
  yellowAvatar:{
    height: 60,
    width: 60,
    marginTop: 20,
    marginLeft: 1860,
    color: '#F0FFFF',
    backgroundColor: "#03C03C",
    position: "absolute"
  }
}





export default App;
